.chatWrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  overflow: hidden;
}
.chatWrapper header {
  -ms-flex-negative: 0;
      flex-shrink: 0;
  text-align: center;
  font-size: 22px;
  padding: 6px;
  border-bottom: 1px solid #ddd;
}

/* BEGIN messageBox */
.messageBox {
  -ms-flex-positive: 1;
      flex-grow: 1;
  overflow-y: auto;
}
/* END messageBox */

/* BEGIN controllers */
.controllers{
  height: 30%;
  border-top: 1px solid #ddd;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}



