/* toolsWrapper editWrapper sendWrapper */
.controllers > div {
  padding: 6px 10px;
  -ms-flex-negative: 0;
      flex-shrink: 0;         /* 当编辑区域高度溢出时，另外两个部分默认不缩小比例 */
}

.controllers > div.editWrapper {
  -ms-flex-negative: 1;
      flex-shrink: 1;         /* 编辑区域高度溢出时，“缩小”此区域 */
  padding: 0 10px;
  overflow-y: auto;
  -ms-flex-positive: 1;
      flex-grow: 1;
}
div.editWrapper > pre {
  white-space: pre-wrap;
  word-wrap: break-word;
  overflow-y: auto;
}

.controllers > div.sendWrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}
div.sendWrapper span {
  color: #555;
  font-size: 13px;
}
div.sendWrapper .sendBtn {
  background-color: #eee;
  padding: 5px 10px;
  border-radius: 4px;
  border: 1px solid #ddd;
}


