.loginWrapper {
  margin: auto;
  padding: 40px 46px;
  background-color: #efefef;
  border-radius: 4px;  -moz-border-radius: 4px;  -webkit-border-radius: 4px;
  box-shadow: 0 2px 10px #999;  -moz-box-shadow: #999 0 2px 10px;  -webkit-box-shadow: #999 0 2px 10px;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.loginWrapper header {
  margin-bottom: 44px;
  width: 150px;
  text-align: center;
  cursor: default;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.loginWrapper header svg {
  width: 100%;
  height: 100%;
  fill: #404956;
  margin: 10px auto;
}

.loginWrapper .nicknameWrapper {
  position: relative;
}
.nicknameWrapper .warning {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: -2px;
  color: red;
  text-align: center;
  font-size: 12px;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
.nicknameWrapper a {
  font-size: 12px;
}

.loginForm .nicknameInput, .loginForm .loginBtn {
  display: block;
  margin: 16px 0;
  padding: 8px;
  border-radius: 20px;
  width: 230px;
  height: 42px;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}
.loginForm .nicknameInput{
  border: 1px solid #999;
  text-align: center;
}
.loginForm .nicknameInput:focus{
  -webkit-box-shadow: 0 2px 10px #ccc;
          box-shadow: 0 2px 10px #ccc;
}
.loginForm .loginBtn{
  cursor: pointer;
  background-color: #404956;
  color: white;
  font-size: 16px;
}
.loginForm .loginBtn:hover{
  background-color: #48566b;
  -webkit-box-shadow: 0 2px 10px #ccc;
          box-shadow: 0 2px 10px #ccc;
}
.loginForm .loginBtn:disabled{
  background-color: #6b7078;
  cursor: not-allowed;
}


